







































































































import { Component, Vue, Ref } from 'vue-property-decorator'
import { Page } from '~/core/decorators'
import { Inject } from 'typescript-ioc'
import { OrderService } from '~/services/order.service'
import { RequestParams } from '~/core/http'
import { PageService } from '~/bootstrap/services/page.service'
import { LoadingService } from '~/bootstrap/services/loading.service'
import DataForm from '~/shared/components/data-form.vue'
import OrderDetail from '~/components/orders/order-detail.vue'
import PageContainer from '../../shared/components/page-container.vue'

@Page({
  name: 'order-page1',
  layout: 'workspace'
})
@Component({
  components: {
    OrderDetail
  }
})
export default class OrderPage1 extends Vue {
  // 表格组件
  @Ref()
  readonly dataForm!: DataForm

  @Ref()
  readonly pageContainer!: PageContainer

  // 订单服务
  private orderService = new OrderService()
  // Loading服务
  private loadingService = new LoadingService()
  // 表格数据源
  private data: any[] = []

  // 表格选择项
  private selectedRowKeys: any[] = []

  // 详情项
  private detail = null

  // 校验规则
  private get rules() {
    return {
      username: [{ required: true, message: this.$t('rules.username_require') }]
    }
  }

  private mounted() {}

  /**
   * 获取订单数据
   */
  private getOrderList() {
    this.dataForm
      .validateFields()
      .then(values => {
        this.orderService
          .getOrderList(new RequestParams(values))
          .subscribe(data => {
            this.data = data
          })
      })
      .catch(err => {
        // 异常处理
      })
  }

  /**
   * 查看订单详情
   */
  private onDetail(detail) {
    this.detail = detail
    this.$nextTick(() => this.pageContainer.scrollToBottom())
  }

  /**
   * 删除订单操作
   */
  private onDelete(id) {}
}
